
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-size: 10px;
    font-family: 'Lato', sans-serif;
    background-color: black;
}

.mainContainer {
    max-width: 1600px;
}

.container{
    max-width: 90%;
    margin: 0 auto;
}

button:focus{outline: 0}
div.headerImg {
    background-image: url(/woody-allen-game/static/media/manhattan.9107ba59.jpg);
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100vh;
}

header {
    text-align: center;
}

header h1, header h2{
    font-size: 10rem;
    text-transform: uppercase;
    text-shadow: 5px 5px white;
}

header h1{
    padding-top: 7rem;
}

header button{
    font-size: 3rem;
    text-transform: uppercase;
    margin-top: 10rem;
    padding: 1rem 2rem;
    background-color: black;
    border: 2px solid black;
    color: white;
    cursor: pointer;
}

header button:hover{
    background-color: white;
    color: black;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

header p{
    font-size: 2rem;
    margin-top: 1rem;
}
.movieSection{
    text-align: center;
    color: white;
    font-size: 1.6rem;
}

.movieSection p:first-child{
    display: inline-block;
    margin: 2rem 0;
    padding: 2rem 10rem;
    border: 5px solid white;
}

.question{
    position: relative;
}

.movieSection .question h4{
    font-size: 3rem;
}

.movieSection .question p:nth-of-type(1){
    margin-top: 1rem;
}

div.imgPlace{
    background-position: center;
    background-size: cover;
    width: 60%;
    height: 60vh;
    margin: 0 auto;
    border: 1rem solid white;
}

.movieSection form input{
    margin: 4rem 0 2rem 0;
    font-size: 3rem;
    text-align: center;
    padding: 1rem;
}

.movieSection form button{
    font-size: 2rem;
    text-transform: uppercase;
    padding: 1rem 2rem;
    background-color: white;
    border: 2px solid black;
    color: black;
    cursor: pointer;
}

.movieSection form button:hover{
    background-color: black;
    color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border: 2px solid white;
}

.getHelp {
    color: white;
    position: absolute;
    bottom: -40%;
    right: 0;
}

.getHelp i{
    -webkit-animation: helpArrow 1s infinite linear;
            animation: helpArrow 1s infinite linear;
}

@-webkit-keyframes helpArrow {
    0%{
        opacity: 0;
        -webkit-transform: translate(0,0);
                transform: translate(0,0);
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        -webkit-transform: translate(0,20px);
                transform: translate(0,20px);
    }
}

@keyframes helpArrow {
    0%{
        opacity: 0;
        -webkit-transform: translate(0,0);
                transform: translate(0,0);
    }
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
        -webkit-transform: translate(0,20px);
                transform: translate(0,20px);
    }
}

.answearItem{
    position: relative;
}

.answearItem div:first-child{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.7);
    width: 100%;
    height: 100vh;
}

.answearItem div:nth-child(2){
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    padding: 5rem;
    border: 5px solid white;
}

.answearItem h1{
    margin: 1rem;
}

.answearItem button{
    margin-top: 2rem;
    font-size: 2rem;
    text-transform: uppercase;
    padding: 1rem 2rem;
    background-color: white;
    border: 2px solid black;
    color: black;
    cursor: pointer;
}

.answearItem button:hover{
    background-color: black;
    color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    border: 2px solid white;
}
.helpingItem, .emptyAlert{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    background-color: white;
    min-width: 70%;
    padding: 2rem;
    border: 2px solid black;
}

.helpingItem h1, .emptyAlert h1{
    font-size: 2rem;
    margin: 2rem;
    color: black;
}

.helpingItem h3{
    font-size: 1.6rem;
    margin: 1rem;
}

.helpingItem ul li{
    list-style: none;
    font-size: 2rem;
}

.helpingItem button, .emptyAlert button, .endGame button{
    text-transform: uppercase;
    padding: 1rem 2rem;
    margin: 2rem;
    background-color: white;
    border: 2px solid black;
    color: black;
    cursor: pointer;
}

.helpingItem button:hover, .emptyAlert button:hover, .endGame button:hover{
    background-color: black;
    color: white;
    border: 2px solid white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.helpingPictures {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
}

.helpingPicture{
    width: 33%;
    background-size: cover;
    background-position: center;
    height: 20rem;
}

.grayBackground{
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.7);
    width: 100%;
    height: 100vh;
}
.emptyAlert p {
    color: black;
}

.endGame{
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: black;
    color: white;
    width: 80%;
    height: 80vh;
    border: 2rem solid white;
}

.endGame h1{
    border-bottom: 2px solid white;
    padding: 2rem 30rem;
    display: inline-block;
}

.endGame div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.endGame div h2 {
    width: 50%;
    padding: 5rem;
}

.endGame div div{
    background-position: center;
    background-size: cover;
    width: 70%;
    height: 400px;
}

div.allenSadPic {
    background-image: url(/woody-allen-game/static/media/allenSad.7c983749.png);
}

div.allenHappyPic {
    background-image: url(/woody-allen-game/static/media/allenHappy.76f742e2.png);
}
div.help {
    margin-top: 4rem;
    text-align: center;
    font-size: 1.6rem;
    color: white;
}
div.helpButtons {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

div.helpButtons button{
    font-size: 2rem;
    margin: 1.5rem;
    background-color: white;
    padding: 1rem 3rem;
    border: 2px solid black;
    text-transform: uppercase;
    cursor: pointer;
}

div.helpButtons button:hover{
    background-color: black;
    color: white;
    border: 2px solid white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}
footer{
    background-color: white;
    width: 100%;
    text-align: right;
    color: black;
}

footer h2{
    padding: 1rem 3rem 1rem 1rem;
}
